import React, { Component } from 'react'
import styles from './Bantuan.css'

class Bantuan extends Component {
  render() {
    const pertanyaan = [
      { 
        id: 0, 
        pertanyaan: 'Apakah NgeKos! sama dengan situs pencari kos lainnya?',
        jawaban: '<p>NgeKos! berbeda dengan situs pencari kos lainnya yang hanya sekedar memberikan informasi umum kos. Disini kami mengkategorikan huniannya berdasarkan jenisnya. Ada 3 jenis hunian yang masih dalam fokus informasi kami yaitu: Kost-kostan, Bangsalan dan Kontrakan. Meskipun layanan utama NgeKos! juga bisa dikatakan memberi informasi yang sama, namun NgeKos! akan berperan dalam penyediaan seluruh kebutuhan yang umumnya dialami oleh anak-anak kos. Adapun yang disediakan ini bertujuan agar memberikan kenyamanan yang mencakup layanan yang dibutuhkan oleh anak-anak kos, seperti tersedianya jasa antar galon, gas, laundry, permak dan lain sebagainya.</p>'
      },
      {
        id: 1,
        pertanyaan: 'Bagaimana cara mencari hunian di NgeKos! untuk tempat / kota tertentu?',
        jawaban: '<p>Untuk mencari hunian di tempat / kota tertentu, Anda cukup ketik nama tempat / kota di kotak pencarian. Hasil pencarian akan menampilkan hunian-hunian seperti kos, bangsal ataupun kontrakan yang tersedia di area tersebut. Anda juga dapat memfilter hasil pencarian berdasarkan harga sewanya. *Perlu diketahui, NgeKos masih dalam masa pengembangan dan akan fokus di kota-kota wilayah Kalimantan Timur seperti Samarinda, Balikpapan dan Kutai Kartanegara.</p>'
      },
      {
        id: 2,
        pertanyaan: 'Sebagai pengusaha kos-kosan, apa keuntungan yang saya dapat jika saya bergabung dengan NgeKos!?',
        jawaban: '<p>NgeKos! akan meningkatkan okupansi setiap hunian yang anda miliki. Informasi lebih lanjut tentang keuntungan bekerjasama dengan NgeKos! dapat dilihat di halaman Mitra.</p>'
      },
      {
        id: 3,
        pertanyaan: 'Sebagai pengusaha penyewaan hunian, bagaimana caranya jika saya ingin bekerjasama dengan NgeKos!?',
        jawaban: '<p>Informasi lengkap tentang cara bekerjasama atau menjadi mitra di NgeKos! dapat disimak di halaman Mitra.</p>'
      },
      {
        id: 4,
        pertanyaan: 'Bagaimana cara menghubungi NgeKos!?',
        jawaban: '<p>Customer Service / Layanan Pelanggan NgeKos! siap melayani Anda selama 24 jam setiap hari melalui email cs@ngekos.co.id, melalui telepon di nomor 0811-555-5573 atau melalui Whatsapp di nomor 0822-5560-3001.</p>'
      },
      {
        id: 5,
        pertanyaan: 'Di mana alamat kantor NgeKos!?',
        jawaban: '<p>Kantor NgeKos! beralamat di Komplek Perumahan Citra Gading Residence, Ruko 20A, Sambutan, Jl. Sultan Sulaiman, Samarinda 77511.</p>'
      }
    ],

    judul = ('Pertanyaan Umum')

    return (
      <div className="isiKonten">
        <h1>{judul}</h1>
        {pertanyaan.map(temp => (
          <div key={temp.id}>
            <h3>{temp.pertanyaan}</h3>
            <div dangerouslySetInnerHTML={{__html: temp.jawaban}} />
          </div>
        ))}
      </div>
    )
  }
}

module.exports = Bantuan


